","$purple-color: #550667;\n$dark-color: #1F2021;\n$font-color: #F6F4EE;\n$green-color: #2F8E70;\n$red-dot: #ff5f57;\n$orange-dot: #F5BF4F;\n$gree-dot: #61C554;\n$link-color: #00F2EA;\n\n$tablet-resolution: 768px;\n$desktop-resolution: 1440px;","@keyframes work-button-down {\n    from {top: 80%}\n    to {top: 0}\n}\n\n@keyframes work-button-top {\n    from {top: 0}\n    to {top: 80%}\n}\n\n@keyframes popup-top {\n    from {transform: translateY(100%)}\n    to {transform: translateY(0);}\n}\n\n@keyframes popup-down {\n    from {transform: translateY(0)}\n    to {transform: translateY(100%);}\n}\n\n// @keyframes destroy-item {\n//     0% {}\n//     50% {\n//         transform: scale(0.5);\n//     }\n//     99% {\n//         transform: scale(0.01);\n//     }\n//     100% {\n//         transform: scale(0);\n//         position: absolute;\n//         width: 0;\n//         height: 0;\n\n//     }\n// }\n\n// .destroyed {\n//     animation: destroy-item 3s ease;\n//     animation-fill-mode: forwards\n// }",".header__wrapper {\n    background-color: $purple-color;\n}\n\n.header {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    margin: 0 20px;\n    margin: 0 6%;\n    max-width: 1200px;\n    @media (min-width: $tablet-resolution) {\n        display: block;\n        margin: 0 22%;\n    }\n    @media (min-width: $desktop-resolution) {\n        margin: 0 auto;\n    }\n    &__logo {\n        display: block;\n        padding: 0 auto;\n        @media (min-width: $tablet-resolution) {\n            display: none;\n        }\n    }\n}\n\n.menu {\n    &__list {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        margin: 0;\n        padding: 0;\n        min-height: 30px;\n        list-style: none;\n        @media (min-width: $tablet-resolution) {\n            min-height: 47px;\n        }\n        @media (min-width: $desktop-resolution) {\n            min-height: 80px;\n        }\n    }\n\n    &__item {\n        display: none;\n        font-size: 14px;\n        font-weight: normal;\n        a {\n            position: relative;\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            width: 100px;\n            min-height: 20px;\n            text-decoration: none;\n            @media (min-width: $tablet-resolution) {\n                width: 145px;\n                min-height: 35px;\n            }\n            @media (min-width: $desktop-resolution) {\n                width: 160px;\n                min-height: 40px;\n            }\n            @include hover-link();\n        }\n        @media (min-width: $tablet-resolution) {\n            display: inline ;\n            font-size: 18px;\n            line-height: 23px;\n        }\n    }\n}\n\n.menu__list .menu__item--mobile {\n    @media (min-width: $tablet-resolution) {\n        display: none;\n    } \n}\n\n.green-text {\n    color: $green-color;\n}\n\n.main {\n    background-color: $dark-color;\n}\n\n.home {\n    &__wrapper {\n        margin: 0 6%;\n        padding-top: 40px;\n        padding-bottom: 10px;\n        max-width: 1200px;\n        @media (min-width: $tablet-resolution) {\n            display: grid;\n            grid-template-columns: 300px 300px;\n            margin: 0 18%;\n            padding-top: 75px;\n            padding-bottom: 20px;\n        }\n        @media (min-width: $desktop-resolution) {\n            margin: 0 auto;\n            grid-template-columns: 600px 600px;\n        }\n    }\n\n    &__title {\n        display: block;\n        margin-bottom: 20px;\n        width: 267px;\n        @include mobile-title();\n        @media (min-width: $tablet-resolution) {\n            @include table-title();\n            grid-column: 1/3;\n            margin-bottom: 40px;\n            width: 500px;\n        }\n        @media (min-width: $desktop-resolution) {\n            @include desktop-title();\n            margin-bottom: 90px;\n            width: 600px;\n        }\n    }\n    \n    &__subtitle {\n        margin-bottom: 10px;\n        @include mobile-subtitle();  \n        @media (min-width: $tablet-resolution) {\n            @include tablet-subtitle();\n            margin-bottom: 15px;\n        }\n        @media (min-width: $desktop-resolution) {\n            margin-bottom: 20px;\n            @include desktop-subtitle();\n        }\n    }\n\n    &__text {\n        margin-bottom: 20px;\n        padding: 0 5%;\n        @include mobile-text();\n        @media (min-width: $tablet-resolution) {\n            margin-bottom: 40px;\n        }\n        @media (min-width: $desktop-resolution) {\n            @include desktop-text();\n            margin-bottom: 60px;\n        }\n    }\n}\n\n.separator {\n    width: 100%;\n    height: 4px;\n    background-color: $purple-color;\n}\n\n.popup {\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    display: grid;\n    grid-template-rows: 75% 25%;\n    width: 100%;\n    height: 80%;\n    background: $dark-color;\n    transform: translateY(100%);\n    animation: popup-down .6s ease;\n\n    &__showed {\n        transform: translateY(0);\n        animation: popup-top .6s ease;\n    }\n\n    &__closed {\n        transform: translateY(100%);\n        animation: popup-down .6s ease;\n    }\n\n    &__list {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        list-style: none;\n        text-align: center;\n        font-weight: normal;\n    }\n\n    &__item {\n        font-weight: normal;\n        color: black;\n    }\n\n    &__button-container {\n        display: flex;\n        justify-content: space-around;\n        align-items: start;\n    }\n\n    &__link {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        padding-top: 4px;\n        width: 100px;\n        min-height: 20px;\n        background-color: $green-color;\n        font-size: 14px;\n        line-height: 18px;\n        text-decoration: none;\n        @media (min-width: $tablet-resolution) {\n            width: 140px;\n            min-height: 30px;\n            font-size: 18px;\n            line-height: 23px;\n        }\n    }\n}\n\n.works {\n    &__wrapper {\n        margin: 0 6%;\n        padding-top: 40px;\n        padding-bottom: 20px;\n        max-width: 1200px;\n        @media (min-width: $tablet-resolution) {\n            margin: 0 18%;\n            padding-top: 60px;\n            padding-bottom: 30px;\n        }\n        @media (min-width: $desktop-resolution) {\n            margin: 0 auto;\n            padding-top: 60px;\n            padding-bottom: 30px;\n        }\n    }\n\n    &__subtitle {\n        margin-bottom: 27px;\n        @include mobile-subtitle();  \n        @media (min-width: $tablet-resolution) {\n            margin-bottom: 35px;\n            @include tablet-subtitle();\n        }\n        @media (min-width: $desktop-resolution) {\n            margin-bottom: 40px;\n            @include desktop-subtitle();\n        }\n    }\n\n    &__button-list {\n        display: grid;\n        grid-template-columns: repeat(auto-fit, 100px);\n        grid-gap: 20px;\n        padding: 0 5%;\n        margin-bottom: 40px;\n        list-style: none;\n        @media (min-width: $tablet-resolution) {\n            grid-template-columns: repeat(auto-fit, 130px);\n            grid-gap: 20px;\n        }\n        @media (min-width: $desktop-resolution) {\n            grid-template-columns: repeat(auto-fit, 160px);\n        }\n    }\n\n    &__button {\n        display: flex;\n        position: relative;\n        align-items: center;\n        justify-content: center;\n        height: 21px;\n        background: transparent;\n        border: none;\n        font-family: inherit;\n        font-size: 14px;\n        font-weight: bold;\n        line-height: 18px;\n        @include hover-link();\n        @media (min-width: $tablet-resolution) {\n            height: 35px;\n            font-size: 18px;\n            line-height: 23px;\n        }\n        @media (min-width: $desktop-resolution) {\n            font-size: 24px;\n            line-height: 30px;\n            height: 40px;\n        }\n    }\n\n    &__popup-container {    \n        position: relative;\n        overflow: hidden;\n        @media (min-width: $desktop-resolution) {\n            &:hover .popup {\n                transform: translateY(0);\n                animation: popup-top .6s ease;\n            }\n            &:hover .works__more-button {\n                top: 0;\n                animation: work-button-down .6s ease;\n            }\n        }\n    }\n\n    &__more-button {\n        position: absolute;\n        bottom: 0;\n        left: 0;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        background-color: $green-color;\n        border: none;\n        width: 100%;\n        height: 20%;\n        font-family: inherit;\n        font-weight: bold;\n        font-size: 14px;\n        line-height: 18px;\n        animation: work-button-top .6s ease;\n\n        &__opened {\n            top: 80%;\n            animation: work-button-top .6s ease;\n        }\n\n        &__closed {\n            top: 0;\n            animation: work-button-down .6s ease;\n        }\n        \n        @media (max-width: $desktop-resolution) {\n            &:active {\n                background-color: scale-color($green-color, $lightness: +15%);\n            }\n        }\n\n        @media (min-width: $tablet-resolution) {\n            font-size: 18px;\n            line-height: 23px;\n        }\n    }\n\n    &__example {\n        &-list {\n            display: grid;\n            grid-template-columns: repeat(auto-fill, 280px);\n            grid-gap: 20px;\n            justify-content: center;\n            list-style: none;\n            @media (min-width: $tablet-resolution) {\n                grid-template-columns: repeat(auto-fill, 392px);\n                grid-gap: 60px;\n            }\n            @media (min-width: $desktop-resolution) {\n                grid-gap: 90px;\n            }\n        }\n        &-item {\n            position: relative;\n        }\n        &-title {\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            min-height: 28px;\n            border-radius: 10px 10px 0px 0px;\n            background-color: white;\n            color: black;\n            text-transform: none;\n            font-weight: normal;\n            font-size: 13px;\n            line-height: 16px;\n        }\n        &-dot {\n            position: absolute;\n            top: 8px;\n            left: 12px;\n            content: '';\n            display: block;\n            height: 12px;\n            width: 12px;\n            background-color: $red-dot;\n            border-radius: 50%;\n            &::before {\n                position: absolute;\n                top: 0;\n                left: 24px;\n                content: '';\n                display: block;\n                height: 12px;\n                width: 12px;\n                background-color: $orange-dot;\n                border-radius: 50%;\n            }\n            &::after {\n                position: absolute;\n                top: 0;\n                left: 48px;\n                content: '';\n                display: block;\n                height: 12px;\n                width: 12px;\n                background-color: $gree-dot;\n                border-radius: 50%;\n            }\n        }\n        &-card {\n            filter: opacity(60%);\n            @media (min-width: $tablet-resolution) {\n                width: 392px;\n                height: 299px;\n            }\n        }\n    }\n}\n\n.contacts {\n    &__wrapper {\n        margin: 0 6%;\n        padding-top: 40px;\n        padding-bottom: 20px;\n        max-width: 1200px;\n        background: url('./../images/earth.png') no-repeat 40px 120px;\n        background-size: 508px auto;\n        @media (min-width: $tablet-resolution) {\n            background-size: 770px auto;\n            margin: 0 18%;\n        }\n        @media (min-width: $desktop-resolution) {\n            margin: 0 auto;\n        }\n    }\n\n    &__subtitle {\n        margin-bottom: 27px;\n        @include mobile-subtitle();  \n        @media (min-width: $tablet-resolution) {\n            margin-bottom: 50px;\n            @include tablet-subtitle();\n        }\n        @media (min-width: $desktop-resolution) {\n            margin-bottom: 140px;\n            @include desktop-subtitle();\n        }\n    }\n\n    &__list {\n        display: grid;\n        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n        padding: 0 5%;\n        margin-bottom: 100px;\n        list-style: none;\n        @media (min-width: $desktop-resolution) {\n            grid-template-columns: repeat(3, 280px);\n            justify-content: space-between;\n            margin-bottom: 200px;\n        }\n    }\n\n    &__item {\n        display: grid;\n        grid-gap: 5px;\n        margin-bottom: 30px;\n    }\n\n    &__name {\n        @media (min-width: $tablet-resolution) {\n            @include tablet-subtitle();\n        }\n        @media (min-width: $desktop-resolution) {\n            @include desktop-subtitle();\n        }\n    }\n\n    &__value {\n        position: relative;\n        text-transform: lowercase;\n        text-decoration: none;\n        font-style: normal;\n        @include mobile-subtitle();\n        span {\n            position: relative;\n            text-transform: lowercase;\n            color: $link-color;\n            &:after {\n                position: absolute;\n                content: '';\n                left: 0;\n                bottom: -2px;\n                background-color: $link-color;\n                display: block;\n                height: 2px;\n                width: 100%;\n                transform: scaleX(0);\n                transform-origi